@import '../../assets/variables';

.animate__animated.animate__flipInX {
  --animate-duration: 0.5s;
}

.icon_arrow_down,
.icon_arrow_up {
  font-size: 1.2rem;
  color: $primary_color1;
}

.mobile {
  display: none;
}

.close_btn {
  font-size: 1.8rem;
  cursor: pointer;
}

.toggle_btn {
  padding: 1rem;
  cursor: pointer;

  span {
    width: 1.8rem;
    background-color: black;
    height: 0.3rem;
  }
}

.close_btn {
  position: absolute;
  top: 0.5rem;
  right: 0.75rem;
}

.no_underline {
  text-decoration: none;
}

.hide {
  display: none;
}

.navbar {
  border-bottom: $primary_color1 solid 2px;
  display: flex;
  gap: 2rem;
  // flex-direction: column;
}

.website_name {
  // margin-right: 5rem;
  a {
    font-size: clamp(0.875rem, -0.1rem + 4.8vw, 1.4rem) !important;
    font-weight: 700 !important;

    &:hover {
      color: $primary_color1;
      text-decoration: underline;
    }
  }
}

.navbar_links {
  display: flex;
  gap: 2rem;
  align-items: center;
  padding-bottom: 1.5rem;
  flex: 1;

  .recipes {
    margin-right: auto;
  }

  li {
    list-style-type: none;
  }

  .link {
    font-size: 1.2rem;
    font-weight: 500;
    cursor: pointer;
  }

  .search {
    font-size: 1.5rem;
  }

  .link:hover {
    color: $primary_color1;
    text-decoration: underline;
  }
}

.dropdown {
  position: relative;
  margin-right: 1.2rem;

  & > div {
    display: flex;
    gap: 0.3rem;
    align-items: center;
  }
}

.dropdown_content {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 2rem;
  left: 1.5rem;
  z-index: 1;
  background-color: white;
  border: 1px solid $primary_color1;

  li {
    padding: 0.5rem 5rem 1rem 1rem;
  }

  a {
    font-size: 1rem;
    white-space: nowrap;
  }

  li:hover {
    background-color: $primary_color1;
  }

  a:hover {
    text-decoration: underline;
  }

  li:nth-child(5) a {
    font-weight: 700;
  }
}

.search_bar {
  position: relative;
  display: flex;
  align-items: center;
  padding-bottom: 1.5rem;

  .search_bar_container {
    display: flex;
    align-items: center;
    width: 0;
    overflow: hidden;
    right: 0;
    transition: width 0.3s ease;
    position: absolute;
    background-color: white;

    &.expanded {
      width: 20rem;
      border: 2px solid #000;
      padding: 0.4rem;
    }
  }

  input {
    flex: 1;
    border: none;
    outline: none;
    padding: 0.5rem;
    background: none;
  }

  .search_button,
  .close_button {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 1.2rem;

    &:hover {
      color: $primary_color1;
    }
  }

  .search_icon {
    background: none;
    border: none;
    outline: none;
    cursor: pointer;
    display: flex;
    height: 100%;
    font-size: 1.5rem;

    &:hover {
      color: $primary_color1;
    }
  }

  .search_icon_hide {
    opacity: 0;
  }
}

.login_button,
.signUp_button {
  padding: 0.3rem 0.3rem;
  border: none;
  cursor: pointer;
  border-radius: 4px;
  font-size: 1.1rem;
  width: 6rem;
}

.login_button {
  background-color: $primary_color1;

  &:hover {
    color: white;
  }
}

.signUp_button {
  background-color: $primary_color3;
  color: white;

  &:hover {
    color: $primary_color1;
  }
}

.favorites {
  display: flex;
  padding-bottom: 1.5rem;
  a {
    font-size: 1.5rem;

    &:hover {
      color: red;
    }
  }
}

@media screen and (max-width: 800px) {
  body {
    position: relative;
  }

  .mobile {
    display: block;
  }

  .toggle_btn {
    display: flex;
    gap: 0.5rem;
    flex-direction: column;
  }

  .button_container {
    display: none;
  }

  .navbar {
    display: flex;
    align-items: center;
  }

  .website_name {
    margin: auto auto;
  }

  .navbar_links.hide {
    display: none;
  }

  .navbar_links.show {
    display: flex;
  }

  .navbar_links {
    flex-direction: column;
    align-items: flex-start;
    justify-content: stretch;
    gap: 2rem;
    position: absolute;
    left: 0;
    top: 0;
    width: 20rem;
    height: max-content;
    padding: 3rem 1rem 3rem 1rem;
    background-color: white;
    z-index: 1;
    border: $primary_color1 solid 2px;

    & > li {
      width: 100%;
    }
  }

  .search_bar_mobile {
    display: flex;
    align-items: center;
    border: 2px solid #000;
    justify-content: center;

    .search_bar_mobile_container {
      display: flex;
      align-items: center;
      background-color: white;

      input {
        border: none;
        outline: none;
        padding: 0.5rem;
        background: none;
      }

      .search_button {
        background: none;
        border: none;
        cursor: pointer;
        font-size: 1.2rem;
        margin-right: auto;

        &:hover {
          color: $primary_color1;
        }
      }
    }
  }

  .mobile_favorites_container {
    background-color: rgba(246, 45, 45, 0.726);
    padding: 1rem 0;
    & > a {
      display: flex;
      justify-content: center;
      gap: 0.8rem;
      color: white;

      &:hover {
        color: black;
      }

      span {
        font-size: 0.7rem;
        font-weight: 700;
      }
    }
  }

  .dropdown_content {
    position: static;
    width: 100%;
    margin-top: 1rem;
    border: none;
    background: none;

    & > li:hover {
      background-color: transparent;
    }
  }

  .favorites {
    padding-bottom: 0;
  }

  .search_bar {
    padding-bottom: 0;
  }
}

@media screen and (max-width: 500px) {
  .favorites {
    display: none;
  }
}
